import { Component, OnInit } from '@angular/core';
import { CustomerDashboardService } from './customer-dashboard.service';
import { FinalFoods } from 'src/app/layout/dietitian/set-food/diet-variables';
import { CommonVariables } from 'src/app/shared/commmon';

@Component({
  selector: 'app-customer-dashboard',
  templateUrl: './customer-dashboard.component.html',
  styleUrls: ['./customer-dashboard.component.scss']
})
export class CustomerDashboardComponent implements OnInit {
  Chef = {ChefName: '', ProfilePic: ''};
  Meals = [];
  DashBoardData: DashboardData = {
    Chef: {},
    Food: []
  };
  follow = true;
  enablePan = true;

  index = 0;
  speed = 3000;
  infinite = true;
  direction = 'right';
  directionToggle = true;
  autoplay = true;
  avatars = [
    { url: '/assets/images/meal.png', title: 'Breakfast' },
    { url: 'https://images-gmi-pmc.edge-generalmills.com/4e0cedae-87fe-4fa1-a066-f332402eab4d.jpg', title: 'Lunch' },
    { url: 'https://s.hswstatic.com/gif/classic-snacks-pictures-1a-622x415.jpg', title: 'Snacks' },
    { url: 'https://i.ndtvimg.com/i/2016-07/tofu-rice-625_625x350_71467969111.jpg', title: 'Dinner' }
  ];

  // lineChart
  public lineChartData: Array<any> = [{ data: [80, 81, 82, 79, 78, 77, 75], label: 'Weight' }];
  public lineChartLabels: Array<any> = [
    '20 DEC 2018',
    '05 JAN 2019',
    '18 JAN 2019',
    '24 JAN 2019',
    '01 FEB 2019',
    '18 FEB 2019',
    '26 FEB 2019'
  ];
  public lineChartOptions: any = {
    responsive: true
  };
  public lineChartColors: Array<any> = [
    {
      // Green
      backgroundColor: 'rgba(231,255,176,0.5)',
      borderColor: 'rgba(169,224,202,1)',
      pointBackgroundColor: 'rgba(169,224,202,1)',
      pointBorderColor: '#fff',
      pointHoverBackgroundColor: '#fff',
      pointHoverBorderColor: 'rgba(169,224,202,.5)'
    }
  ];
  public lineChartLegend: boolean;
  public lineChartType: string;

  constructor(private service: CustomerDashboardService, private util: CommonVariables) {
    this.GetCustomerDashboardData();
  }
  openGallery($event) {
    console.log($event);
  }

  switchIndex($event) {
    console.log($event);
  }

  ngOnInit() {
    this.lineChartLegend = true;
    this.lineChartType = 'line';
  }
  // get customer Dashboard data
  GetCustomerDashboardData() {
    this.service.GetCustomerDashboardData().subscribe(Response => {
      if (Response.Status === true) {
          this.DashBoardData = Response.Data;
          this.Chef = this.DashBoardData.Chef;
          this.Meals = this.DashBoardData.Food;
      }
    });
  }
  // get chef profile pic
  GetImageLocation(filename) {
    let FinalLocation = '';
    if (filename == null) {
      FinalLocation = '/assets/images/chef-placeholder.png';
    } else {
      FinalLocation = this.util.base_url + filename;
    }
    return FinalLocation;
  }
  // get dish images
  FilterCarousalImages(filename) {
    let FinalLocation = '';
    if (filename == null) {
      FinalLocation = 'url(/assets/images/recipe-placeholder.jpg)';
    } else {
      FinalLocation = 'url(' + this.util.base_url + filename + ')';
    }
    return FinalLocation;
  }
  // LIVE HEALTHY
  ScrollToBottom(elemId) {
const element: HTMLElement = document.getElementById(elemId); element.scrollIntoView({behavior: 'smooth'});
  }

}
export interface DashboardData {
  Chef;
  Food;
}
