import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { CommonVariables } from 'src/app/shared/commmon';
import { CommonResponse } from 'src/app/shared/commmon/ServiceClass';
import { map } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class CustomerDashboardService {

constructor(private http: HttpClient, private util: CommonVariables) { }
// get customer dashboard
GetCustomerDashboardData() {
const url = this.util.GetCustomerDashboardData;
return this.http.get<CommonResponse>(url).pipe(map((response: CommonResponse) => response));
}
}
